@import 'css/config.scss';

.mouse {
  &__container {
    z-index: 25;
    right: 2vw;
    position: absolute;
    top: 50%;
    transform: translateX(-50%);
    display: flex;
    align-items: center;
    animation: float 2s infinite ease-in-out;
    transition: opacity 1s ease-out;
    opacity: 1;
    cursor: pointer;
    pointer-events: all;
    &.fade-out {
      opacity: 0;
      pointer-events: none;
    }
  }
  &__mouse {
    position: relative;
    width: 24px;
    border-radius: 11px;
    height: 40px;
    border: white 2px solid;
  }
  &__wheel {
    position: absolute;
    top: 6px;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 2.5px;
    width: 5px;
    height: 7px;
    background: $accent;
  }
  &__arrow-container {
    margin: 0 0 0 10px;
  }
  &__arrow-top {
    width: 10px;
    height: 3px;
    border-radius: 1.5px;
    background: $light;
    transform: rotate(45deg) translateY(-2px);
  }
  &__arrow-bottom {
    width: 10px;
    height: 3px;
    border-radius: 1.5px;
    background: $light;
    transform: rotate(-45deg) translateY(2px);
  }
}

@keyframes float {
  from {
    transform: translateX(-5px);
  }

  50% {
    transform: translateX(5px);
  }

  to {
    transform: translateX(-5px);
  }
}
